.App {
}

.container {
  max-width: 1200px;
  margin: 0 auto;
}

.header {
  background-color: #222222;
  padding: 10px 0px;
}

.header__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__logo {
  font-size: 22px;
  font-weight: 700;
  transition: all 0.2s ease;
}

.header__logo span {
  padding: 10px;
  color: #121212;
  background: -webkit-linear-gradient(
    90deg,
    #f54b64,
    #f78361
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    90deg,
    #f54b64,
    #f78361
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  /* transition: all 0.3s ease; */
}

.header__logo:hover {
  opacity: 80%;
}
.header__nav {
}

.header__list {
  display: flex;
  align-items: center;
}

.header__item + .header__item {
  margin-left: 35px;
}

.header__link {
  transition: all 0.3s ease;
  padding: 13px 20px;
}

.header__link:hover {
  background: -webkit-linear-gradient(
    90deg,
    #f54b64,
    #f78361
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(90deg, #f54b64, #f78361);
  color: #121212;
}

/* ==========================Even week=================================== */

.evenWeek {
  padding: 25px 0px;
}

.evenWeek__title {
  text-align: center;
}

.dayCards {
  padding: 40px 0px;
  display: flex;

  justify-content: space-around;
}

.dayCard {
  color: #121212;
  border-radius: 15px;
  text-align: center;
  max-width: 250px;
  padding: 35px 15px;
  background: -webkit-linear-gradient(90deg, #cbbf5c, #c68a51);
  background: linear-gradient(90deg, #cbbf5c, #c68a51);

  transition: all 0.3s ease;
  cursor: pointer;
}

.dayCard:hover {
  opacity: 70%;
}

.dayCard__title {
  font-size: 22px;
}

.dayCard__subject-list {
  margin-top: 20px;
}

.dayCard__subject-item + .dayCard__subject-item {
  margin-top: 20px;
}

.dayCard__subject-item span {
  margin-right: 10px;
  padding: 5px;
  border-radius: 10px;
  background-color: #222222;
  color: #fff;
  opacity: 40%;
}

/* =================Адаптив================ */

@media screen and (max-width: 1200px) {
  .container {
    max-width: 960px;
  }
}

@media screen and (max-width: 992px) {
  .container {
    max-width: 720px;
  }
}
@media screen and (max-width: 768px) {
  .container {
    max-width: 540px;
  }

  .header {
    position: sticky;
    top: 0;
  }

  .header__logo {
    font-size: 18px;
  }

  .header__item + .header__item {
    margin-left: 15px;
  }

  .header__link {
    font-size: 15px;
  }

  .dayCards {
    flex-direction: column;

    align-items: center;
    padding-bottom: 0px;
  }

  .dayCard + .dayCard {
    margin-top: 15px;
  }
}
@media screen and (max-width: 576px) {
  .container {
    max-width: 90%;
  }
  .header {
    padding: 6px 0px;
  }
  .header__logo {
    font-size: 12px;
  }
  .header__logo span {
    padding: 10px 3px;
  }

  .header__list {
  }

  .header__item + .header__item {
    margin-left: 7px;
  }

  .header__link {
    font-size: 11px;

    padding: 0px 0px;
  }

  .header__link:hover {
    background: none;
    color: #fff;
    opacity: 80%;
  }

  .evenWeek__title {
    font-size: 22px;
  }

  .dayCard__title {
    font-size: 16px;
  }
  .dayCard {
    font-size: 15px;
  }
}
